*,
*::after,
*::before {
    box-sizing: inherit;
    margin: 0;
    padding: 0;
}

html {
    /* This defines what one rem is */
    font-size: 62.5%;
}

body {
    box-sizing: border-box;
    overflow-x: hidden;
}

img {
    width: 100%;
}

.fab {
    font-family: "Font Awesome 5 Brands" !important;
}
